@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300;400;600&display=swap);
html {
	height: 100%;
	font-size: 62.5%;
}

body {
	height: 100%;
	padding: 0;
	margin: 0;

	display: flex;
	align-items: center;
	justify-content: center;

	font-family: "Work Sans", sans-serif;
}

* {
	box-sizing: border-box;
}

html {
	font-family: "Work Sans", sans-serif;
}

:root {
	--header-size: 2.2rem;
	--text-size: 1.4rem;

	--clr-red: #ea4335;
	--clr-blue: #4285f4;
	--clr-yellow: #fbbc05;
	--clr-green: #34a853;

	--clr-red-transparent: #ea443527;
	--clr-divider: #a8a4a4;
	--clr-outline: #545252;

	--font-light: 300;
	--font-semibold: 600;
	--font-regular: 400;

	--clr-red-fill: invert(35%) sepia(57%) saturate(1854%) hue-rotate(336deg)
		brightness(92%) contrast(99%);
	--clr-blue-fill: invert(51%) sepia(30%) saturate(5587%) hue-rotate(200deg)
		brightness(97%) contrast(97%);
	--clr-green-fill: invert(51%) sepia(88%) saturate(372%) hue-rotate(84deg)
		brightness(90%) contrast(86%);
	--clr-divider-fill: invert(79%) sepia(4%) saturate(166%) hue-rotate(314deg)
		brightness(83%) contrast(89%);
}

.global-checkbox {
  font-size: 4rem;
  border: 2px solid #212121;
  border-radius: 50%;
  height: 2rem;
  width: 2rem;
  cursor: pointer;
  transition: all 0.167s;
}
